import axios from 'axios';
import React, { useState, useEffect } from 'react';
import { useHistory, useParams } from 'react-router-dom';
import styled from 'styled-components';
import './Detail.scss';

//CSS    ,   
// class . 
// :   class    class   .
// : styled-components css  .
let Box = styled.div`
    padding: 20px;
`;

let Title = styled.h4`
    font-size: 25px;
    color: ${ props => props.color }
`;

function Detail(props) {

    let [alert, changeAlert] = useState(true);
    let [inputData, ChangeInputData] = useState('');

    //  Hook:       .
    useEffect( () => {
        //  mount, update  
        // axios.get() //   api  useEffect .  []  .

        let timer = setTimeout( () => { changeAlert(false)}, 2000); //2 alert 
        console.log('');
        //  , 
        //  return function () {  } 
        return () => { clearTimeout(timer)} //   =>    setTimeout  .
    }, [alert]); //  []    . alert '' ,
    // []       
    
    // useParams 
    let { id } = useParams(); //   URL  
    let history = useHistory(); 
    let searchedItem = props.shoes.find( function(oneShoes) { return oneShoes.id == id }); 
    //filter       , find     
    //return    
    return(
        <div className="container">
            <Box>
                {/* <Title color = { 'red' }></Title>
                <Title color = "blue" ></Title> */}
                <Title className = "red" ></Title>
            </Box>
            {inputData}
            <input onChange = { (e) => { ChangeInputData(e.target.value) } }/>
            {/* state       */}

            {  // : state    state   
               //  UI    .
                alert === true
                ? (<div className="my-alert2">
                    <p>   .</p>
                  </div>)
                : null
            }
            
            <div className="row">
                <div className="col-md-6">
                    <img src={ 'https://codingapple1.github.io/shop/shoes' + ( searchedItem.id + 1 ) + '.jpg' } width="100%" />
                </div>
                <div className="col-md-6 mt-4">
                    <h4 className="pt-5">{ searchedItem.title }</h4>
                    <p>{searchedItem.content}</p>
                    <p>{searchedItem.price}</p> 

                    <Info stock = {props.stock}></Info>

                    <button className="btn btn-danger" onClick = { () => { 
                        props.changeStock([ [...props.stock][0] - 1, [...props.stock][1], [...props.stock][2]]) 
                    }}></button>
                    <button className="btn btn-danger" onClick={ () => {
                        // history.goBack();
                        history.push('/');
                    }}></button>
                </div>
            </div>
        </div>
    )
}

function Info(props) { // App  Detail  Info
    return (
        <p>: {props.stock[0]}</p>
    )
}

